body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: green;
}

#root,
#Game-table {
  width: 100%;
  height: 100%;
}

.upperDiv {
  height: 50%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.upperDivLeft {
  width: 40%;
}

.upperDivRight {
  width: 60%;
}

.upperDivLeft,
.upperDivRight {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.foundation {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

#total {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.stocks .club,
.stocks .heart,
.stocks .spade,
.stocks .diamond,
.waste .club,
.waste .heart,
.waste .spade,
.waste .diamond,
.foundationPiles .club,
.foundationPiles .heart,
.foundationPiles .spade,
.foundationPiles .diamond {
  width: 100px;
  height: 130px;
  font-size: 35px;
  margin: 0%;
  position: fixed;
}

.club,
.heart,
.spade,
.diamond {
  width: 100px;
  height: 130px;
  font-size: 35px;
  border: 1px solid black;
  border-radius: 8px;
  position: relative;
  margin-top: -80px;
}

.stocks,
.waste,
.foundationPiles,
.tableauPiles {
  width: 140px;
  height: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.stocks,
.waste,
.foundationPiles {
  height: 200px;
  width: 140px;
  background-color: brown;
  border: 1px solid gray;
}

.upperPartOfCard,
.symbol,
.lowerPartOfCard {
  height: 33%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  background-color: whitesmoke;
}

.upperPartOfCard {
  text-align: left;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.symbol {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.lowerPartOfCard {
  text-align: right;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.tableau {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.heart,
.diamond {
  color: red;
}

.spade,
.club {
  color: black;
}

.undo {
  font-size: 65px;
}

